@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.top {
    height: 8vh;
    width: 100%;
    background-color: black;
    color: white;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 2em
}

.top .ant-col{
    text-align: center;
}

.gold{
    color: #FFD700;
}

.underline:after{
    display:block;
    content: '';
    border-bottom: solid 1px white;  
    -webkit-transform: scaleX(0);  
            transform: scaleX(0);
    transition: -webkit-transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
}

.underline:hover{
    cursor: pointer;
    color: #bfbfbf;
}

.underline:hover:after{ -webkit-transform: scaleX(1); transform: scaleX(1); }

.search-icon{
    display: -webkit-flex;
    display: flex;
    margin-left: auto;
}

.search-icon:hover{
    cursor: pointer;
}

.menu-icon:hover{
    cursor: pointer;
}


.search-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-left: 0.5em;
}
.search .ant-input:focus{
    outline:none !important;
    outline-width: 0 !important;
    box-shadow: none;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
}
.search .ant-input:hover{
    border: 1px solid #d9d9d9;
}
.search .ant-btn{
    background-color: #d9d9d9;
    border: none;
}
.search .ant-btn:hover{
    background-color: #bfbfbf;
}
.cover{
  height: 100vh;
  width: 100%;
}

.left{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.details{
  background-color: black;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-left: 3em;
}

.title{
  font-size: 2.5em;
  padding-top: 20%;
}

.subtitle{
  font-size: 4.5em;
}

.category{
  text-transform: uppercase;
  font-size: 1.25em;
  letter-spacing: 0.5em;
  padding-bottom: 1em;
}

.description{
  display: -webkit-flex;
  display: flex;
  margin-bottom: 0.5em;
}

.description-first{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-weight: bold;
  border-right: 1px solid white;
  padding: 0.5em;
}

.description-second{
  padding: 1em;
  text-align: justify;
}

.image{
  min-height: 35%;
  background: url(https://i.insider.com/50ca49fdecad04d76b00002d?width=900&format=jpeg&auto=webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.latest-news-container{
    background-color: white;
    padding: 2em 4em;
}

.latest-news-container__top{
    margin: 1em;
}

.latest-news-container__top:hover{
    cursor: pointer;
}

.latest-news-container__top:hover .latest-news-container__top__bottom .anticon{
    visibility:visible;
    opacity:1;
}

.latest-news-container__top .ant-col{
    margin: 0.5em 0;
}

.latest-news-container__top img{
    width: 100%;
    height: auto;
}

.latest-news-container__top__span{
    margin: 0.5em 0;
    font-weight: 500;
}

.latest-news-container__top__bottom{
    width: 100%;
    margin: 1em 0.5em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: #757575;
}

.latest-news-container__top__bottom svg{
    padding-right: 1em;
    height: 120%;
    width: 150%;
    color: #f759ab;
}

.latest-news-container__top__bottom .anticon{
    visibility:hidden;
    opacity:0;
    transition:visibility 0.3s linear,opacity 0.3s linear;
}

.latest-news-container__top h2{
    font-weight: bold;
}

.latest-news-container__top p{
    text-align: justify;
}

.latest-news-container__middle{
    margin: 2em 1em;
}

.latest-news-container__middle:hover{
    cursor: pointer;
}

.latest-news-container__middle:hover .latest-news-container__middle__details__bottom .anticon{
    visibility:visible;
    opacity:1;
}

.latest-news-container__middle h3{
    padding-top: 0.25em;
    font-weight: bold;
}

.latest-news-container__middle__img{
    width: 100%;
    height: auto;
}

.latest-news-container__middle__details__title{
    color: #292929;
    font-weight: 500;
}

.latest-news-container__middle__details__title img{
    height: 20px;
    width: 20px;
    border-radius: 100%;
    margin-right: 10px;
}

.latest-news-container__middle__details__bottom{
    font-weight: normal;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-right: 1em;
    -webkit-align-items: center;
            align-items: center;
    color: #757575;
}

.latest-news-container__middle__details__bottom svg{
    padding-right: 1em;
    height: 120%;
    width: 120%;
    color: #f759ab;
}


.latest-news-container__middle__details__bottom .anticon{
    visibility:hidden;
    opacity:0;
    transition:visibility 0.3s linear,opacity 0.3s linear;
}
*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  user-select: none; /* standard syntax */
    -webkit-user-select: none; /* webkit (safari, chrome) browsers */
    -moz-user-select: none; /* mozilla browsers */
    -khtml-user-select: none; /* webkit (konqueror) browsers */
    -ms-user-select: none; /* IE10+ */
}
