.cover{
  height: 100vh;
  width: 100%;
}

.left{
  display: flex;
  flex-direction: column;
}

.details{
  background-color: black;
  flex-grow: 1;
  color: white;
  display: flex;
  flex-direction: column;
  padding-left: 3em;
}

.title{
  font-size: 2.5em;
  padding-top: 20%;
}

.subtitle{
  font-size: 4.5em;
}

.category{
  text-transform: uppercase;
  font-size: 1.25em;
  letter-spacing: 0.5em;
  padding-bottom: 1em;
}

.description{
  display: flex;
  margin-bottom: 0.5em;
}

.description-first{
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  border-right: 1px solid white;
  padding: 0.5em;
}

.description-second{
  padding: 1em;
  text-align: justify;
}

.image{
  min-height: 35%;
  background: url(https://i.insider.com/50ca49fdecad04d76b00002d?width=900&format=jpeg&auto=webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}